.App {
  text-align: left;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1{ 
  text-align: center;
  padding-bottom: 30px;
}

.btn-teste { 
  animation: go-back 1s;
}



@keyframes go-back {
  0% {
      transform: translateX(100px);
  }
  100% {
      transform: translateX(0);
  }
}

.teste{ 
  background-color: black;
}
